import { Component, h, Prop } from '@stencil/core';

@Component({
  tag: 'ks-contact',
})
export class Contact {

  @Prop() phone: string;
  @Prop() email: string;

  render() {

    return(
        <div class="uk-text-center uk-text-emphasis" style={{padding: "50px"}}>
            <div class="uk-visible@s" style={{height: '20px'}}></div>
            <slot/>
            <ks-contact-buttons phone={this.phone} email={this.email}></ks-contact-buttons>
        </div>
    );
  }
}